import { immerable } from "immer";
import { generate } from "shortid";
import { Bead, BeadTypes } from "./Editor";
import { DeepPartial } from "ts-essentials";
import { buildPoint } from "./Point";
import { merge } from "lodash";
import { defaulBeadColor } from "../components/colors";

const SEPARATOR = "__";
export const ID_BEAD = `bead`;

export enum BeadChildSuffix {
  ANY = "beadChildOf",
}

export const getThreeNameFromBeadId = (id: string, childSuffix?: BeadChildSuffix) => {
  return `${ID_BEAD}${SEPARATOR}${id}${SEPARATOR}${childSuffix}`;
};

export const getBeadIdFromThreeName = (name?: string) => {
  if (!name) {
    return undefined;
  }
  if (name.includes(ID_BEAD) === false) {
    return undefined;
  }
  return name.split(SEPARATOR)[1];
};

export const buildBead = (props: DeepPartial<Bead>): Bead => {
  const defaults: Bead = {
    id: generate(),
    position: buildPoint(0, 0),
    type: BeadTypes.TOHO_15,
    // rotate: 0,
    rotateX: 90,
    rotateY: 0,
    rotateZ: 0,
    lineRotate: 0,
    color: defaulBeadColor,
    height: 10,
    width: 10,
    depth: 10,
    keepAspectRatio: false,
    [immerable]: true,
  };

  return merge(defaults, props);
};
