import { immerable } from "immer";
import { generate } from "shortid";
import { Bead, BeadTypes, EditBBox } from "./Editor";
import { DeepPartial } from "ts-essentials";
import { buildPoint } from "./Point";
import { merge } from "lodash";
import { defaulBeadColor } from "../components/colors";


const SEPARATOR = "__";
export const ID_REGION = "EditBBox";
export enum EditBBoxChildSuffix {
  BG = "BG",
  EDIT_CONTAINER = "EDIT_CONTAINER",
  LINES = "LINES",
  DOT_RESIZE_TOP_LEFT = "DOT_RESIZE_TOP_LEFT",
  DOT_RESIZE_TOP_RIGHT = "DOT_RESIZE_TOP_RIGHT",
  DOT_RESIZE_BOTTOM_LEFT = "DOT_RESIZE_BOTTOM_LEFT",
  DOT_RESIZE_BOTTOM_RIGHT = "DOT_RESIZE_BOTTOM_RIGHT",
}

export const getThreeNameFromEditBBoxId = (
  id: string,
  childSuffix?: EditBBoxChildSuffix
) => {
  return `${ID_REGION}${SEPARATOR}${id}${SEPARATOR}${childSuffix}`;
};

export const getEditBBoxIdFromThreeName = (name?: string) => {
  if (!name) {
    return undefined;
  }
  if (name.includes(ID_REGION) === false) {
    return undefined;
  }
  return name.split(SEPARATOR)[1];
};

export const buildEditBBox = (props: DeepPartial<EditBBox>): EditBBox => {
    const defaults: EditBBox = {
      id: generate(),
      position: buildPoint(0, 0),
      rotateX: 0,
      rotateY: 0,
      rotateZ: 0,
      height: 0,
      width: 0,
      depth: 10,
      editable: true,
      beadIds: [],
      regionsIds: [],
      [immerable]: true,
    };
  
    return merge(defaults, props);
  };
  