import * as THREE from "three";
import { initEventEmitterHandlers } from "./eventEmitterHandlers";
import { loadAppModels } from "./loadModels";
import { initMouseListeners } from "./mouseListeners/mouseListeners";

export let appThree: AppThree;

export const renderThreejs = (el: HTMLElement | null) => {
  initCanvasApp(el);
};

const initCanvasApp = (el: HTMLElement | null) => {
  if (el === null) {
    console.warn("no html el element found");
    return;
  }

  try {
    appThree = new AppThree(el);
    el.appendChild(appThree.renderer.domElement);
    loadAppModels(appThree);
    initMouseListeners();
    initEventEmitterHandlers(appThree.scene);
    requestAnimationFrame(threeRender);
  } catch (err) {
    console.error("err:", err.toString());
  }
};

class AppThree {
  el: HTMLElement;
  camera: THREE.OrthographicCamera;
  scene: THREE.Scene;
  renderer: THREE.WebGLRenderer;
  constructor(el: HTMLElement) {
    this.el = el;
    const { width, height } = el.getBoundingClientRect();

    this.camera = new THREE.OrthographicCamera(
      width / -2,
      width / 2,
      height / 2,
      height / -2,
      0,
      1000
    );

    this.camera.position.set(0, 0, 500);
    this.camera.lookAt(0, 0, 0);

    this.scene = new THREE.Scene();
    this.scene.background = new THREE.Color("#F1F3F3");

    this.renderer = new THREE.WebGLRenderer({ antialias: true });
    this.renderer.setPixelRatio(window.devicePixelRatio);

    this.renderer.setSize(width, height);
  }
}

function threeRender() {
  if (needToResizeRendererToDisplaySize(appThree.renderer)) {
    appThree.camera.updateProjectionMatrix();
  }

  appThree.renderer.render(appThree.scene, appThree.camera);
  requestAnimationFrame(threeRender);
}

function needToResizeRendererToDisplaySize(renderer) {
  const canvas = renderer.domElement;
  const width = canvas.clientWidth;
  const height = canvas.clientHeight;
  const needResize = canvas.width !== width || canvas.height !== height;
  if (needResize) {
    renderer.setSize(width, height, false);
  }
  return needResize;
}
